import React, { useState } from 'react';
import { useAuth0 } from "../react-auth0-spa"
import { Link } from "react-router-dom"
import { api } from "../config"

import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Typography from '@material-ui/core/Typography';
import IconButton from '@material-ui/core/IconButton';
import KeyboardArrowUpIcon from '@material-ui/icons/KeyboardArrowUp';
import KeyboardArrowDownIcon from '@material-ui/icons/KeyboardArrowDown';


const useStyles = makeStyles({
    root: {
        maxWidth: 345,
        background: '#FAF5FB'
    },
});

export default function SinglePost({ post }) {
    const [upvotes, setUpvotes] = useState(post.Votes.filter(vote => vote.upVote).length);
    const [downvotes, setDownvotes] = useState(post.Votes.filter(vote => vote.downVote).length);

    const { user, getTokenSilently } = useAuth0()
    const classes = useStyles();

    const upVoteHandler = async (e) => {
        if (user) {
            const token = await getTokenSilently();
            let res = await fetch(`${api}/posts/${post.id}/upvote`, {
                method: "PATCH",
                headers: {
                    Authorization: `Bearer ${token}`,
                    "Content-Type": "application/json"
                },
                body: JSON.stringify({ userId: user.userId })
            });
            //sets local state so front end displays the votes dynamically
            if (res.status === 204) {
                setUpvotes(upvotes - 1);
            }
            if (res.status === 201) {
                setUpvotes(upvotes + 1);
            }
            if (res.status === 206) {
                setUpvotes(upvotes + 1);
                setDownvotes(downvotes - 1);
            }
        } else {
            return;
        }
    }
    const downVoteHandler = async (e) => {
        if (user) {
            const token = await getTokenSilently();
            let res = await fetch(`${api}/posts/${post.id}/downvote`, {
                method: "PATCH",
                headers: {
                    Authorization: `Bearer ${token}`,
                    "Content-Type": "application/json"
                },
                body: JSON.stringify({ userId: user.userId })
            });
            if (res.status === 204) {
                setDownvotes(downvotes - 1);
            }
            if (res.status === 201) {
                setDownvotes(downvotes + 1);
            }
            if (res.status === 206) {
                setDownvotes(downvotes + 1);
                setUpvotes(upvotes - 1);
            }
        } else {
            return;
        }
    }

    return (
        <Card className={classes.root} style={{ margin: '10px' }}>
            <Link to={{
                pathname: `/posts/${post.id}`,
                post
            }} >
                <CardActionArea >
                    <CardMedia
                        component="img"
                        alt={post.title}
                        height="180"
                        image={post.imageUrl}
                        title={post.title}
                    />
                    <CardContent>
                        <Typography variant="h5" component="h2">
                            {post.title}
                        </Typography>
                        {/* <Typography variant="body2" color="textSecondary" component="p">
                            {post.description}
                        </Typography> */}
                    </CardContent>
                </CardActionArea>
            </Link>
            <CardActions>
                <IconButton onClick={upVoteHandler}>
                    <KeyboardArrowUpIcon />
                    <Typography variant="subtitle1">{upvotes}</Typography>
                </IconButton>
                <IconButton onClick={downVoteHandler}>
                    <KeyboardArrowDownIcon />
                    <Typography variant="subtitle1">{-1 * downvotes}</Typography>
                </IconButton>
            </CardActions>
        </Card >
    );
}
